.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 15px;

}
.dialogsItems a {
    color: darkslategray;
    text-decoration: none;
}

.dialogsItems .active{
    color: firebrick;
}
.messages {
    padding: 21px;
    color: darkslategray;
}
.img {
    height: 40px;
    width: 40px;
    border-radius: 25px;
    margin-top: 15px;

}
.header{
    font-style: normal;
margin-left: 400px;

}
.button{
    padding: 10px 15px;
    margin: 5px 0 20px 0;
    font-size: 15px;
    color : #fff;
    background-color: tomato;
    border: none;
    border-bottom: 4px solid #ffa082;
    cursor: pointer;
}
.button:hover {
    transition: all 0.5s;
    background: #fff;
    color: firebrick;
}